<template>
  <div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Name</th>
          <th>Aliases</th>
          <th>Price</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(vaccine, key) in vaccines" :key="key">
          <td>{{ vaccine.name }}</td>
          <td>{{ vaccine.aliases.map((alias) => alias.value).join(", ") }}</td>
          <td>{{ vaccine.price.toFixed(2) }}</td>
          <td>
            <a class="button is-link" @click="discounts(vaccine)">Discounts</a>
          </td>
        </tr>
      </tbody>
    </table>
    <vaccinediscounts-component></vaccinediscounts-component>
  </div>
</template>
<script>
import VaccineDiscountsComponent from "@/components/VaccineDiscountsComponent";

export default {
  name: "ExamsDiscountComponent",
  created() {
    this.$store.dispatch(
      "fetchDiscountVaccines",
      this.$store.state.authentication.inMemoryToken
    );
  },
   methods: {
      discounts(vaccine) {
          this.$store.dispatch("updateCashierCurrentVaccine", vaccine);
          this.$store.dispatch("toggleCashierVaccineModal", true);
      }
  },
  computed: {
    vaccines() {
      return this.$store.state.cashier.vaccines;
    }
  },
  components: {
      "vaccinediscounts-component": VaccineDiscountsComponent
  }
};
</script>