<template>
  <div class="modal" :class="{ 'is-active': active }" v-if="vaccine">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Vaccine: {{ vaccine.name }}</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div style="margin-bottom: 1em;">
          <div class="field is-horizontal">
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded">
                  <input
                    class="input"
                    type="text"
                    v-model="fields.name"
                    placeholder="Name"
                  />
                </div>
              </div>
              <div class="field">
                <div class="control is-expanded">
                  <input
                    class="input"
                    type="number"
                    v-model.number="fields.value"
                    placeholder="Value(percentage)"
                  />
                </div>
              </div>
              <div class="field">
                <div class="control">
                  <button
                    class="button is-fullwidth is-link is-outlined"
                    @click="add"
                  >
                    Add discount
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="box">
          <table class="table is-fullwidth" v-if="vaccine.discounts.length > 0">
            <thead>
              <th></th>
              <th>Name</th>
              <th>Value</th>
            </thead>
            <tbody>
              <tr v-for="(discount, key) in vaccine.discounts" :key="key">
                <td>
                  <button
                    class="button is-danger is-outlined"
                    @click="remove(discount)"
                  >
                    <span class="icon is-small">
                      <i class="far fa-trash-alt"></i>
                    </span>
                  </button>
                </td>
                <td>{{ discount.name }}</td>
                <td>{{ discount.value }}</td>
              </tr>
            </tbody>
          </table>
          <div v-else>
            <strong>Vaccine has no discount</strong>
          </div>
        </div>
        <button
          class="button is-medium is-fullwidth is-link"
          @click="update(vaccine)"
        >
          Update vaccine
        </button>
      </section>
    </div>
  </div>
</template>
<script>
export default {
  name: "VaccineDiscountsComponent",
  data() {
    return {
      fields: {
        name: null,
        value: null
      }
    };
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleCashierVaccineModal", false);
      this.$store.dispatch("updateCashierCurrentVaccine", null);
    },
    remove(discount) {
      let index = -1;
      let discounts = this.$store.state.cashier.currentVaccine.discounts;
      for (let i = 0; i < discounts.length; i++) {
        if (discounts[i].name === discount.name) {
          index = i;
        }
      }
      if (index != -1) {
        discounts.splice(index, 1);
      }
    },
    add() {
      this.$store.state.cashier.currentVaccine.discounts.push(this.fields);
    },
    update(vaccine) {
      this.$socket.emit("update-vaccine", vaccine);
      this.closeModal();
    }
  },
  computed: {
    vaccine() {
      return this.$store.state.cashier.currentVaccine;
    },
    active() {
      return this.$store.state.cashier.isVaccineModalOn;
    }
  }
};
</script>
